import { Controller } from '@ali/4ever-cangjie';
import { isKeyHotkey } from 'is-hotkey';
import toggleUnderline from '../commands/toggleUnderlineMark';

const isUnderlineHotKey = isKeyHotkey('mod+u');

export default function onKeyDown(event, controller: Controller, next) {
  if (isUnderlineHotKey(event)) {
    return toggleUnderline(controller);
  }
  return next();
}
